import React from "react"

const noteColumn = () => {
  const notes = [
    "B",
    "A",
    "G",
    "F",
    "E",
    "D",
    "C",
    "B",
    "A",
    "G",
    "F",
    "E",
    "D",
    "C"
  ]
  return (
    <div className="column column--note">
      {notes.map(note => (
        <div className="block">
          <div className="block-text">{note}</div>
        </div>
      ))}
    </div>
  )
}

export default noteColumn



