const initialState = {
  isPlayMusic: false
}

const headerReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHANGE_PLAY_STATUS":
      return {
        isPlayMusic: true
      }
    default:
      return state
  }
}

export default headerReducer



